import 'uni-pages';import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false
Vue.prototype.getUrlParam = function(name) {
	let reg = new RegExp('(^|&)'+ name + '=([^&]*)(&|$)')
	let r = window.location.search.substr(1).match(reg)
	if(r!=null){
		return unescape(r[2])
	} 
	return null
}
App.mpType = 'app'

const app = new Vue({
	...App
})
app.$mount()